body{
    margin:0;
}
#root{
    padding-top:56px;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card{
    text-align:center;
}
.card-content{
    padding:0px !important;
}
.card-title{
    padding:0
}
.card-img{
    height:140px
}



.CollectionItem {
    background: url("../img/ramka.jpg") no-repeat scroll center center
    transparent;
    width: 150px;
    height: 188px;
    float: left;
    padding: 12px 14px;
}
.CollectionItem img {
    left: 29px;
    top: 26px;
    position: relative;
}
.CollectionItem div {
    position: relative;
    top: 29px;
    width: 100px;
    height: 40px;
    left: 26px;
    overflow: hidden;
    text-align: center;
}
.ObjImg img{
    width:50%;
    max-width:300px;
}


.page-view{
    width:100%;
    overflow:auto;
}
.page-view img{
    width:100%;
}
